/* 请求 Loading 样式 */
.request-loading {
  min-width: 100px;
}
.request-loading .ant-spin-text {
  margin-top: 5px;
  font-size: 18px;
  color: #509ff1;
}
.request-loading .ant-spin-dot-item {
  background-color: #509ff1;
}
/* 请求 Loading 遮罩层样式 */
#loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  background: rgba(0, 0, 0, 0.5);
}
